import React, { useState } from "react";
import './TagsInput.css';    
import $ from 'jquery';

const TagsInput = props => {
    const [tags, setTags] = React.useState(props.tags);
    const removeTags = indexToRemove => {
        setTags([...tags.filter((_, index) => index !== indexToRemove)]);
        props.selectedTags([...tags.filter((_, index) => index !== indexToRemove)]);
    };
    const addTags = event => {
        if (event.target.value !== "") {

            if (tags.find(tag => tag.toLowerCase().trim() === event.target.value.toLowerCase().trim())) {
                return;
            }
            setTags([...tags, event.target.value]);
            props.selectedTags([...tags, event.target.value]);
            event.target.value = "";
        }
    };
    var txtbox = $('input[id="reportTags"]');
txtbox.change(function () {
    txtbox.removeAttr('placeholder');
});
if(tags.length==0)
{
    function placeholders() {
        document.querySelector("input[id=reportTags]")
        .setAttribute("placeholder", "Please Enter Tags");
      }
      $(document).ready(placeholders);
    };
    return (
        <div className="tags-input">
            <ul id="tags">
                {tags.map((tag, index) => (
                    <li key={index} className="tag">
                        <span className='tag-title'>{tag}</span>
                        <span className='tag-close-icon' onClick={() => removeTags(index)}>x</span>
                    </li>
                ))}
            </ul>
            <input id="reportTags"
                type="text"
                onKeyUp={event => event.key === "Enter" ? addTags(event) : null}
                placeholder="Please Enter Tags"
            />
        </div>
    );
};
export default TagsInput;